.btns-theory {
    border-color:  #ff70a6;
    border-radius: 12px;
    border: 2px solid #ff70a6;
    color:  #ff70a6;
    position: relative;
    overflow: hidden;
    z-index: 1;
    transition: color 150ms ease-in-out;
  }
  .btns-theory:after {
    content: '';
    position: absolute;
    display: block;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 100%;
    background: #ff70a6;
    z-index: -1;
    transition: width 150ms ease-in-out;
  }
  .btns-theory:hover {
    color: #fff;
  }
  .btns-theory:hover:after {
    width: 110%;
  }

