  overflow: hidden;
  align-items: flex-start;
  justify-content: center;
}
.col-2{
  max-width: 50%;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
}
.col-3{
  max-width: 33.333333%;
  display: flex;
