}
svg {
  stroke-width: 1!important;
}
.relative{
  position: relative;
}
.absolute{
  position: absolute;
}
strong{
  color: #ff7ca5;
  font-weight: 600;
}
input{
  color: rgba(198,201,216,.75);
}
button.btn {
  font-size: 15px;
  font-weight: 500;
  font-family: 'Yusei Magic', sans-serif;
  border-radius: 5px;
  border: 2px solid #83dff2;
  background-color: #83dff2;
  color: #fff!important;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: .4s all ease;
  height: 50px;
  width: 180px;
  margin: 0px 10px;
  
}
.p__color {
  color: rgb(0, 0, 0);
  font-size: 18px;
  line-height: 30px;
  font-family: 'Yusei Magic', sans-serif;
  margin-bottom: 15px;
}
.white{
